import { useState } from 'react';

// @mui
import {
  Stack,
  Popover,
  MenuItem,
  Typography,
  Box,
  Chip,
  Divider,
  Pagination,
  Grid,
  useTheme,
  ListItemIcon
} from '@mui/material';

//  third-party
import { filter, uniq, ceil, size, includes } from 'lodash';

// components
import PageContainer from 'layouts/PageContainer';
import PageHeader from 'layouts/PageHeader';

// import { fDate } from 'utils/formatTime'
import BlogListToolbar from './sections/BlogListToolbar';
import BlogCard from './sections/BlogCard';
import BlogTopCard from './sections/BlogTopCard';

// mock
import { POSTS, CATEGORY, STATUS } from '__mocks__/blogs';
import { IconPencil, IconTrash } from '@tabler/icons';

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query, filterConditions) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query || filterConditions) {
    return filter(array, (_item) => {
      return (
        _item?.title?.toLowerCase().indexOf(query.toLowerCase()) !== -1 &&
        includes(
          size(filterConditions.category) === 0
            ? CATEGORY
            : filterConditions.category,
          _item.category
        ) &&
        includes(
          size(filterConditions.status) === 0
            ? STATUS
            : filterConditions.status,
          _item.status
        )
      );
    });
  }
  return stabilizedThis.map((el) => el[0]);
}

const rowsPerPage = 6;
const Blog = () => {
  const theme = useTheme();
  // const navigate = useNavigate();
  const [open, setOpen] = useState(null);

  const [page, setPage] = useState(0);

  const [filterName, setFilterName] = useState('');
  const [filterConditions, setFilterConditions] = useState({
    category: [], //CATEGORY,
    status: [] //STATUS
  });

  const items = POSTS;

  const handleCloseMenu = () => {
    setOpen(null);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage - 1);
  };

  const handleFilterByName = (event) => {
    setPage(0);
    setFilterName(event.target.value);
  };

  const handleFilterCondition = (type, condition) => {
    setPage(0);

    setFilterConditions((preConditions) => {
      let category = preConditions.category;
      let status = preConditions.status;
      if (type.toLowerCase().indexOf('category') !== -1) {
        category = uniq([...preConditions.category, condition]);
      }
      if (type.toLowerCase().indexOf('status') !== -1) {
        status = uniq([...preConditions.status, condition]);
      }
      return { category, status };
    });
  };

  const filteredItems = applySortFilter(
    items,
    getComparator('', ''),
    filterName,
    filterConditions
  );

  // const isNotFound = !filteredItems.length && !!filterName;

  const handleConditionDelete = (type, condition) => {
    setPage(0);
    setFilterConditions((preConditions) => {
      let category = preConditions.category;
      let status = preConditions.status;
      if (type?.toLowerCase()?.indexOf('category') !== -1) {
        category = filter(
          category,
          (_item) => _item?.indexOf(condition) === -1
        );
      }
      if (type?.toLowerCase()?.indexOf('status') !== -1) {
        status = filter(status, (_item) => _item?.indexOf(condition) === -1);
      }

      return { category, status };
    });
  };

  return (
    <PageContainer>
      <PageHeader
        title="Blog"
        control={{ label: 'New Post', route: '/pages/blogs/create' }}
      />
      <Box sx={{ flexGrow: 1 }}>
        <Stack direction="row" spacing={1} sx={{ ml: 3 }}>
          {filterConditions.category.map((item) => (
            <Chip
              key={item}
              label={
                <Stack direction={'row'}>
                  <Typography>Category: </Typography>
                  <Typography color="warning.main">{item}</Typography>
                </Stack>
              }
              onDelete={() => handleConditionDelete('category', item)}
              sx={{
                borderRadius: `${theme.shape.borderRadius}px`
              }}
            />
          ))}

          {filterConditions.status.map((item) => (
            <Chip
              key={item}
              label={
                <Stack direction={'row'}>
                  <Typography>Status: </Typography>
                  <Typography color="warning.main">{item}</Typography>
                </Stack>
              }
              onDelete={() => handleConditionDelete('status', item)}
            />
          ))}
          <Divider />
        </Stack>
        <BlogListToolbar
          numSelected={0}
          filterName={filterName}
          onFilterName={handleFilterByName}
          onFilterCondition={handleFilterCondition}
        />

        {/* <Scrollbar> */}
        <Grid container rowGap={3}>
          <Grid item xs={12}>
            <BlogTopCard
              key={filteredItems[0]?.id}
              post={filteredItems[0]}
              index={0}
            />
          </Grid>
          <Grid item xs={12}>
            <Typography variant="h2">Latest Articles</Typography>
            <Typography variant="body1">
              The Blog is is a frank and funny conversation about what it means
              to work in development.
              <br />
            </Typography>
            <Divider />
          </Grid>
          <Grid item xs={12}>
            <Grid container spacing={3}>
              {filteredItems.slice(1, 4).map((row, index) => {
                // const { id, title, media, stock, price, status, createdAt } = row;
                return (
                  <Grid key={row?.id} item xs={12} md={4}>
                    <BlogCard post={row} index={index} />
                  </Grid>
                );
              })}
            </Grid>
          </Grid>
          <Grid item xs={12}>
            <Typography variant="h2">Popular Articles</Typography>
            <Typography variant="body1">
              Here's what's trending recently.
            </Typography>
            <Divider />
          </Grid>
          <Grid item xs={12}>
            <Grid container spacing={3}>
              {filteredItems
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row, index) => {
                  // const { id, title, media, stock, price, status, createdAt } = row;
                  return (
                    <Grid key={row?.id} item xs={12} md={6}>
                      <BlogCard post={row} index={index} />
                    </Grid>
                  );
                })}
            </Grid>
          </Grid>
          <Grid item xs={12}>
            <Stack alignItems="center" sx={{ mt: 3 }}>
              <Pagination
                count={ceil(filteredItems.length / rowsPerPage)}
                onChange={handleChangePage}
                color="primary"
                size="large"
                showFirstButton
                showLastButton
              />
            </Stack>
          </Grid>
        </Grid>
        <Popover
          open={Boolean(open)}
          anchorEl={open}
          onClose={handleCloseMenu}
          anchorOrigin={{ vertical: 'top', horizontal: 'left' }}
          transformOrigin={{ vertical: 'top', horizontal: 'right' }}
          PaperProps={{
            sx: {
              p: 1,
              width: 140,
              '& .MuiMenuItem-root': {
                px: 1,
                typography: 'body2',
                borderRadius: `${theme.shape.borderRadius}px`
              }
            }
          }}
        >
          <MenuItem>
            <ListItemIcon fontSize="small">
              <IconPencil />
            </ListItemIcon>
            Edit
          </MenuItem>
          <MenuItem sx={{ color: 'error.main' }}>
            <ListItemIcon fontSize="small">
              <IconTrash />
            </ListItemIcon>
            Delete
          </MenuItem>
        </Popover>
      </Box>
    </PageContainer>
  );
};

export default Blog;
