import PropTypes from 'prop-types';
// @mui
import { styled, alpha, useTheme } from '@mui/material/styles';
import {
  Toolbar,
  Typography,
  OutlinedInput,
  InputAdornment,
  Stack,
  Box,
  useMediaQuery
} from '@mui/material';
// component

import Menu from 'components/menu';
// mocks
import { CATEGORY, STATUS } from '__mocks__/blogs';
import { IconSearch } from '@tabler/icons';

// ----------------------------------------------------------------------

const StyledRoot = styled(Toolbar)(({ theme }) => ({
  height: 96,
  display: 'flex',
  justifyContent: 'space-between',
  padding: theme.spacing(0, 1, 0, 3)
}));

const StyledSearch = styled(OutlinedInput)(({ theme }) => ({
  width: 240,
  flexGrow: 1,
  transition: theme.transitions.create(['box-shadow', 'width'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter
  }),
  '&.Mui-focused': {
    width: 320
    // boxShadow: theme.customShadows.z8,
  },
  '& fieldset': {
    borderWidth: `1px !important`,
    borderColor: `${alpha(theme.palette.grey[500], 0.32)} !important`
  }
}));

// ----------------------------------------------------------------------

BlogListToolbar.propTypes = {
  numSelected: PropTypes.number,
  filterName: PropTypes.string,
  onFilterName: PropTypes.func,
  onFilterCondition: PropTypes.func
};

export default function BlogListToolbar({
  numSelected,
  filterName,
  onFilterName,
  onFilterCondition
}) {
  const theme = useTheme();
  const matchDownMd = useMediaQuery(theme.breakpoints.down('md'));
  // const matchDownSm = useMediaQuery(theme.breakpoints.down('sm'));
  return (
    <StyledRoot
      sx={{
        ...(numSelected > 0 && {
          color: 'text.default',
          bgcolor: 'background.default'
        })
      }}
    >
      {numSelected > 0 ? (
        <Typography component="div" variant="subtitle1">
          {numSelected} selected
        </Typography>
      ) : (
        <Box
          sx={{
            width: 1,
            display: 'flex',
            flexDirection: matchDownMd ? 'column' : 'row',
            justifyContent: 'flex-start',
            alignItems: matchDownMd ? 'flext-start' : 'center'
          }}
        >
          <Box sx={{ flexGrow: 1 }}>
            <StyledSearch
              value={filterName}
              onChange={onFilterName}
              placeholder="Search user..."
              startAdornment={
                <InputAdornment position="start">
                  <IconSearch
                    size={20}
                    color={theme.palette.grey[400]}
                    stroke={2}
                    strokeLinejoin="miter"
                  />
                </InputAdornment>
              }
            />
            {/* <BlogsSearch posts={POSTS} /> */}
          </Box>
          <Stack direction={'row'} spacing={2}>
            <Menu
              handleMenuItemChecked={onFilterCondition}
              caption="Category"
              items={CATEGORY}
            />
            <Menu
              handleMenuItemChecked={onFilterCondition}
              caption="Status"
              items={STATUS}
            />
            {/* <CustomizedMenus handleFilterCondition={onFilterCondition} caption="Stock" items={["All","Available","Out Of Stock"]} /> */}
          </Stack>
        </Box>
      )}

      {/* {numSelected > 0 ? (
        <Tooltip title="Delete">
          <IconButton>
             <IconTrash />
          </IconButton>
        </Tooltip>
      ) : (
        <Tooltip title="Filter list">
          <IconButton>
             <IconFilter size={20} color={theme.palette.grey[400]}   stroke={2}   strokeLinejoin="miter" />
          </IconButton>
        </Tooltip>
      )} */}
    </StyledRoot>
  );
}
