import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';
// @mui
import { useTheme } from '@mui/material/styles';
import {
  Box,
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia,
  Typography
} from '@mui/material';
// utils
import { fDate } from 'utils/formatTime'; //'../../../utils/formatTime';
import { fShortenNumber } from 'utils/formatNumber';
//

import AvatarWithUser from 'components/avatar/AvatarWithUser';
import { IconEye, IconMessageCircle, IconShare } from '@tabler/icons';

// ----------------------------------------------------------------------

const BlogCard = ({ post, index }) => {
  const { id, title, cover, content, view, comment, share, author, createdAt } =
    post || {};
  const theme = useTheme();
  const navigate = useNavigate();
  const POST_INFO = [
    // { number: comment, icon: 'eva:message-circle-fill' },
    { number: comment, icon: <IconMessageCircle /> },
    { number: view, icon: <IconEye /> },
    { number: share, icon: <IconShare /> }
  ];

  return (
    <Card>
      <CardActionArea onClick={() => navigate(`/pages/blogs/${id}`)}>
        <CardMedia
          component="img"
          height={260}
          image={cover}
          alt="Paella dish"
        />
        <CardContent>
          {/* <Box
            component="img"
            height="260"
            src={cover}
            sx={{
              borderRadius: `${theme.shape.borderRadius}px`,
              my: 1
            }} */}
          {/* /> */}
          {/* <StyledCardMedia >
        <StyledCover alt={title} src={cover} />
        </StyledCardMedia> */}

          <Typography
            variant="h5"
            sx={{ color: theme.palette.text.default }}
            gutterBottom
          >
            {title?.substr(0, 60)}
            {title.length > 60 ? '...' : ''}
          </Typography>
          <Typography
            gutterBottom
            variant="body1"
            sx={{
              height: 66,
              color: 'text.secondary',
              display: 'block',
              overflow: 'hidden'
            }}
          >
            {content?.substr(0, 120)}
            {content.length > 160 ? '...' : ''}
          </Typography>
          <AvatarWithUser
            name={author.name}
            avatar={author.avatarUrl}
            createdAt={fDate(createdAt)}
          />
          {/* <StyledInfo> */}

          {/* </StyledInfo> */}
        </CardContent>
      </CardActionArea>
      <CardActions sx={{ justifyContent: 'flex-end' }}>
        {POST_INFO.map((info, index) => (
          <Box
            key={index}
            sx={{
              display: 'flex',
              alignItems: 'center',
              ml: index === 0 ? 0 : 1.5,
              mr: 1,
              gap: 0.5,
              color: theme.palette.grey[500]
            }}
          >
            {info.icon}
            <Typography variant="caption">
              {fShortenNumber(info.number)}
            </Typography>
          </Box>
        ))}
      </CardActions>
    </Card>
  );
};

BlogCard.propTypes = {
  post: PropTypes.object.isRequired,
  index: PropTypes.number
};

export default BlogCard;
