const BlogTopCard = ({ post, index }) => {
  const { id, title, cover, content, author, createdAt } = post || {};
  const theme = useTheme();
  const navigate = useNavigate();
  const matchDownMd = useMediaQuery(theme.breakpoints.down('md'));

  // const handleDetail = (id) => {
  //   navigate(`/pages/blogs/${id}`)
  // }

  return (
    <Card
      sx={{ display: 'flex', flexDirection: matchDownMd ? 'column' : 'row' }}
      onClick={() => navigate(`/pages/blogs/${id}`)}
    >
      <CardMedia
        component="img"
        height={260}
        image={cover}
        alt="blog"
        sx={{
          height: 300,
          width: matchDownMd ? 1 : 2 / 5,
          // gridColumn: '1/6',
          borderRadius: `${theme.shape.borderRadius}px`,
          borderTopRightRadius:
            (matchDownMd && `${theme.shape.borderRadius}px`) || 0,
          borderBottomRightRadius:
            (matchDownMd && `${theme.shape.borderRadius}px`) || 0
        }}
      />

      <CardContent>
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            gap: 2,
            p: 1
          }}
        >
          <Typography
            variant="h5"
            sx={{ color: theme.palette.text.default }}
            gutterBottom
          >
            {title?.substr(0, 240)}
            {title?.length > 240 ? '...' : ''}
          </Typography>
          <Typography
            gutterBottom
            variant="body1"
            sx={{
              height: 66,
              color: 'text.secondary',
              display: 'block',
              overflow: 'hidden'
            }}
          >
            {content?.substr(0, 560)}
            {content?.length > 560 ? '...' : ''}
          </Typography>
          <Box sx={{ flexGrow: 1 }} />
          <AvatarWithUser
            name={author?.name}
            avatar={author?.avatarUrl}
            createdAt={fDate(createdAt)}
          />
        </Box>
      </CardContent>
    </Card>
  );
};

BlogTopCard.propTypes = {
  post: PropTypes.object.isRequired,
  index: PropTypes.number
};

export default BlogTopCard;
